*{
    padding: 0;
    margin: 0;
}
body{
    background-color: ivory;
}
.container{
    width: 90%;
    margin: auto;
    padding-top: 100px;
    display: flex;
}
.left-content,.right-content{
    flex-basis: 50%;
}
.left-content{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(200px,0.5fr));
    grid-gap: 30px;
}
.card{
    width: 260px;
    background-color:#9EDEF9;
    border-radius: 1rem;
    box-shadow: 0 5px 5px rgba(0,0,0,0.5);
    padding: 0 1.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
}
.right-content{
    width: 50%;
    text-align: center;
    color: black;
    position: fixed;
    top:100px;
    right: 10px;
}
.right-content img{
    margin: 1.5rem;
    height: 160px;
}
.right-content h1{
    font-weight: bold;
    letter-spacing: 1px;
}
.types{
    width: 70%;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
}
.types .type-group{
    background-color: #b74555;
    color: white;
    padding: 0.5rem;
    font-size: 12px;
    border-radius: 8px;
    margin-left: 1rem;
}
.base-stat{
    margin-top: 2rem;
}
.btn-group{
    display: flex;
}
.btn-group button{
    outline: none;
    border: none;
    width: 150px;
    padding: 0.3rem 0;
    margin: 1rem;
    font-size: 17px;
    font-weight: bold;
    color: white;
    background-color: #b74555;
    border-radius: 8px;
}

.navbar{
    width: 100%;
    background-color: #b74555;
    border: 2px;
    border-style: solid;
    border-color: black;
    margin: 0 auto;
    display: table;
}

.navbar-links{
    font-size: 25px;
    display: inline-block;
    padding: 10px;
    list-style-type: none;
}

.navbar-list{
    text-align: center;
}

a:link{
    text-decoration: none;
    color: ivory;
}

a:visited{
    color: ivory;
}

.about{
    text-align: center;
    margin-top: 20px;
}